
#picker{
    height: 50px;
    width: 50px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border-radius: 50%;
    background-color: transparent;
    border: none;
 
}
#picker::-webkit-color-swatch{
    border-radius: 50%;
    border: 2px solid #2F4F4F
}
#picker::-moz-color-swatch{
    border-radius: 50%;
    border: 2px solid #2F4F4F;
}
